import  axios  from  'axios'


const game = {
  namespaced: true,
  state: {
    games: [],
    game: {},
    loading: false
  },
  mutations: {
    SET_GAMES (state, payload) {
      state.games = payload
    },
    SET_GAME (state, payload) {
      state.game = payload
    },
    SET_LOADING (state, payload) {
      state.loading = payload
    }
  },
  getters: {
    GET_GAMES: (state) => {
      return state.games
    },
    GET_GAME: (state) => {
      return state.game
    },
    GET_LOADING: (state) => {
      return state.loading
    }
  },
  actions: {
    async fetchGames ({state, commit}) {
      state.loading = true
      let {data} = await axios.get(`http://localhost/game`, {
        withCredentials: true
      })
      commit('SET_GAMES', data)
      state.loading = false
    },
    async fetchGame ({state, commit}, id) {
      state.loading = true
      let {data} = await axios.get(`http://localhost/game/${id}`, {
        withCredentials: true
      })
      commit('SET_GAME', data)
      state.loading = false
    },
    async create ({commit}) {
      let {data} = await axios.get(`http://localhost/game/new`, {
        withCredentials: true
      })
      commit('SET_GAME', data)
    },
    async attack ({commit}, id) {
      let {data} = await axios.put(`http://localhost/game/${id}`, {
        withCredentials: true
      })
      commit('SET_GAME', data)
    }
  }
}

export default game
