<template>
    <v-container>
        <v-layout
                column
                justify-center
                align-center
        >
            <v-flex mb-4>
                <h1 class="display-2 font-weight-bold mb-3">
                    Welcome to Vuetify
                </h1>
                <p class="subheading font-weight-regular">
                    For help and collaboration with other Vuetify developers,
                    <br>please join our online
                    <a href="https://community.vuetifyjs.com" target="_blank">Discord Community</a>
                </p>
            </v-flex>
            <v-flex
                    xs12
                    sm8
                    md6
            >
                <v-card>
                    <v-card-text>
                        <v-btn @click="createGame()">Create Game</v-btn>
                        <v-btn @click="getGames()">Get Games</v-btn>
                        <table>
                            <tr v-for="(game, key) in games" :key="key">
                                <td>{{ key }}</td>
                                <td>{{ game.progress }}/{{ game.maxHealth}}</td>
                                <td>
                                    <v-btn @click="getGame(key)">Play</v-btn>
                                </td>
                                <td>
                                    <v-btn @click="attack(key)">Attack</v-btn>
                                </td>
                            </tr>
                        </table>
                    </v-card-text>
                </v-card>
                <v-container v-if="game" grid-list-md text-xs-center>
                    <v-layout row wrap>
                        <v-flex xs3 :xs12="building.name === 'Castle'" v-for="(building, key) in game.buildings"
                                :key="key">
                            <v-card>
                                <h3>{{ building.name }}</h3>
                                <v-alert
                                        :value="building.target"
                                        :type="building.hit ? 'success' : 'danger'"
                                >
                                    Shot {{ building.hit ? 'Hit!' : 'Missed!' }}
                                </v-alert>
                                <v-progress-circular
                                        size="70" width="5"
                                        :value="((building.health / building.maxHealth) * 100)"
                                        :color="((building.health / building.maxHealth) * 100) < 40 ? 'red' : 'green'"
                                >
                                    {{ building.health }} hp
                                </v-progress-circular>
                            </v-card>
                        </v-flex>
                    </v-layout>

                </v-container>

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
  import {mapState, mapGetters} from 'vuex'
  export default {
    mounted: function () {
      this.$store.dispatch('Game/fetchGames')
    },
    computed: {
      ...mapState({
        games: 'Game/games',
        game: 'Game/game'
      }),
      ...mapGetters({
        games: 'Game/GET_GAMES',
        game: 'Game/GET_GAME'
      })
    },
    methods: {
      createGame () {
        this.$store.dispatch('Game/create')
      },
      getGames () {
        this.$store.dispatch('Game/fetchGames')
      },
      getGame (id) {
        this.$store.dispatch('Game/fetchGame', id)
      },
      attack (id) {
        this.$store.dispatch('Game/attack', id)
      }
    },
    data: () => {
      return {
        inGame: false
      }
    }
  }
</script>

